input[type="checkbox"] {
  position:relative;
  width: 50px;
  height: 20px;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background-color: #FF6B00;
  outline: none;
  border-radius: 20px;
  box-shadow: inset 0 0 5px rgba(0,0,0,.2);
  transition: .5s;
}

input:checked[type="checkbox"] {
  background-color: royalblue;
}

input[type="checkbox"]:before {
  content: '';
  position: absolute;
  width: 20px;
  height: 20px;
  border-radius: 20px;
  top: 0;
  left: 0;
  background-color: #fff;
  transform: scale(1.1);
  box-shadow:  0 2px 5px rgba(0,0,0,.2);
  transition: .5s;
}

input:checked[type="checkbox"]:before {
  left: 30px;
}

.active-celsius {
  color: #f25900;
}

.active-fahrenheit {
  color: royalblue;
}
